:root {
  --headerHeight: 56px;
  --footerHeight: 56px;
  --defaultPadding: 24px;
}
html,
body,
#root,
.App {
  height: 100%;
}

.full-screen {
  width: 100%;
  height: 100vh;
}
body::-webkit-scrollbar {
  display: none;
}

[id*="sec"] {
  height: 100vh;
  background-color: aquamarine;
  border-bottom: 1px solid black;
  box-sizing: border-box;
}

[class*="flex"] {
  display: flex;
}

[class*="flex-col"] {
  flex-direction: column;
}
