#intro {
  max-width: 393px;
  width: calc(100% - 140px);
  padding: 0 70px;
  gap: 8px;
}

#intro > div:nth-child(1) {
  font-size: clamp(10px, 6vw, 36px);
  font-weight: 700;
  gap: 4px;
}

#intro > div:nth-child(2) > img {
  max-width: 100%;
}

#intro > div:nth-child(3) {
  width: 100%;
  text-align: right;
  font-size: clamp(10px, 5.2vw, 33px);
  gap: 16px;
}

#intro > div:nth-child(3) > div:nth-child(n + 2) {
  border-bottom: 8px solid #d9d9d9;
  padding: 8px 0;
  font-size: clamp(10px, 4.8vw, 24px);
  cursor: pointer;
  text-align: left;
}
#intro > div:nth-child(3) > div:nth-child(n + 2):hover {
  border-bottom: 8px solid #3eebad;
  font-weight: 600;
  color: #3eebad;
}

div#content {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
}
