main {
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 500px;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}

header {
  height: var(--headerHeight);
  width: 100%;
  background-color: aqua;
}

section#content {
  width: 100%;
  flex-grow: 1;
  background-color: crimson;
}

footer {
  height: var(--footerHeight);
  width: 100%;
  background-color: blanchedalmond;
}
